:root {
    --header-bgColor: rgba(233, 76, 0, 0.7);
    --boxShadow: 0 1px 3px 0 rgba(0, 0, 0, 0.33);
}
div#root {
    width: 100%;
}
* {
    outline: none;
    text-decoration: none;
}

html {
    font-family: sans-serif;
}

body {
    /* font-family: "merriweather-regular", serif; */
    font-family: "merriweather-light", serif;
    background-color: rgba(128, 128, 128, 0.19) /* #80808029 */;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    height: 100%;
    margin:0;
}

header {
    height: 69px; /* 48px; */
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-left: 15%;
    margin-right: 15%;
    /* background-color: #ff4500ab; */
    background-color: var(--header-bgColor);
    color:white;
    -webkit-box-shadow: var(--boxShadow);
    box-shadow: var(--boxShadow);

    padding-left: 56px;
    padding-right: 56px;
    box-sizing: border-box;
}

header h2 {
    font-family: "merriweather-bold", serif;
    font-size: 1.7rem;
    line-height: 0;
}

.header-left {
    width: 100%;
}

.container {
    width: 100%;
    margin-top: 0;

    display:flex;
    /* width: inherit; */
    flex-direction: column;
    justify-content: center;
    /* margin-top: 15px; */
    align-items: center;			
}

button {
    background-color: orangered;
    color: white;
    /* padding: 6px 12px; */
    padding: 13px 24px;
    font-size: 14px;
    margin-bottom: 1px;
    border-radius: 4px;
    background-image: none;
    border: 1px solid transparent;
    font-family: "raleway-heavy", sans-serif;
    cursor: pointer;
}

button[disabled] {
    opacity: 0.5;
    cursor: none;
}

.button-block {
    width: 100% !important;
    margin-top: 15px;
    margin-bottom: 15px;
    padding-top: 11px;
    padding-bottom: 11px;
}

.button-small {
    padding: 3px 6px;
    font-size: 10px;
}

a {
    text-decoration: none;
    word-wrap: none;
    white-space: nowrap;
}

.float-right {
    float: right !important;
}

.profile-section {
    width: 100%;
    display: flex;
    flex-direction: row-reverse;
}

.dropdown {
    background-color: rgba(0,0,0,0.1) !important;
    color: white !important;    
    position: relative;
}

.dropdown-menu {
    background: white;
    list-style: none;
    display: flex;
    flex-direction: column;
    position: absolute;

    justify-content: flex-start;
    align-items: flex-start;
    z-index: 100;

    margin: 0;
    padding: 0;
    box-sizing: border-box;

    border-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.33);
    animation: slide-up 0.3s ease-out;
    /* min-width: 115px; */

    top: 100%;
    right: 0;
    font-family: raleway-medium;
}

.dropdown-menu .dropdown-menu-item {
    display: block;
    text-align: center;
    padding-top: 4px;
    padding-bottom: 4px;
    color: black;
    cursor: pointer;

    width: 100%;
    margin: 0;
}

.dropdown-menu a {
    /* padding: 10px; */
    background: transparent;
    /* width: inherit; */
    display: block;
    text-decoration: none;

    text-align: center;
    padding: 5px 20px;

    color: orangered;
}

.dropdown-menu .dropdown-menu-item :hover {
    text-decoration: none;
    color: #252727;
    background-color: #f5f5f5;
}

.dropdown-menu .divider {
    height: 1px;
    margin: 11px 0;
    overflow: hidden;
    background-color: #e5e5e5;
    width: 100%;
}

.spinner {
    width: 10px;
    height: 10px;
    display: inline;
}

.spinner .spinner-item {
    background-color: orangered;
    width: 9px;
    height: 9px;
    display: inline-block;
    border-radius: 50%;
}

.spinner .spinner-item:nth-child(1) {
    animation-name: spinAnim;
    animation-duration: 1s;
    animation-delay: -0.24s;
    animation-iteration-count: infinite;
}

.spinner .spinner-item:nth-child(2) {
    animation-name: spinAnim;
    animation-duration: 1s;
    animation-delay: -0.12s;
    animation-iteration-count: infinite;
}

.spinner .spinner-item:nth-child(3) {
    animation-name: spinAnim;
    animation-duration: 1s;
    animation-delay: 0s;
    animation-iteration-count: infinite;
}

@keyframes spinAnim {
    0% { transform: scale(0); }
    100% { transform: scale(1); }
}

@media(max-width: 500px) {
    .container {
        width: 100%;
    }

    header {
        padding-left: 20px;
        padding-right: 5px;
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .header-left {
        width: 58%;
    }

    .dropdown-menu {
        top: 5%;
    }

    .profile-section {
        flex-direction: row;
    }
}
