.smancard {
    border-radius: 3px;
    box-shadow: 0 3px 5px 0 rgba(0, 2, 3, 5);
    display: flex;
    flex-direction: column;
    font-family: 'Courier New', Courier, monospace;
    padding: 5px;
    overflow-x: scroll;
    background: linear-gradient(45deg, white, rgb(215, 141, 91));
    width: 600px;
}

.smancard h1 h2 h3 h4 h5 h6 {
    font-family: 'Courier New', Courier, monospace;
}

.smancard-header {
    display: flex;
    flex-direction: row;
    box-sizing: content-box;
    padding-left: 3px;
    padding-right: 3px;
    width: 600px;
}

.smancard-header-details {
    white-space: nowrap;
    flex: 1;
    font-size: 0.7em;
    padding-left: 3px;
    padding-right: 3px;
    box-sizing: border-box;
}

.smancard-header-pic {
    /*flex: 1;*/
    padding-left: 3px;
    padding-right: 3px;
}

.smancard-body {
    display: flex;
    flex-direction: row;
    padding: 3px;
    padding-top: 20px;
    padding-bottom: 9px;
    width: 600px;
}

.sman-body-details {
    display: flex;
    flex-direction: row;
    padding-left: 10px;
    justify-content: space-between;
    width: 100%;
    position: relative;
}

.sman-body-min-detail {
    padding-left: 45px;
}

.sman-body-min-detail-text{
    padding-top: 3px;
    padding-bottom: 3px;
}

@media(max-width: 400px) {
    .smancard {
        width: 100%;

    }
    .smancard-body {
        width: 100%;
    }

    .smancard-header {
        width: 100%;
    }

    .smancard-header-details h1 h2 {
        font-size: 10px;
    }

    .smancard-header-pic {
        width: 84px;
    }

    .sman-pin-pic {
        width: 50px;
    }

    .smancard-header-details-title {
        font-size: 15px;
    }

    .smancard-header-details-motto {
        font-size: 14px;
    }
    .sman-body-min-detail {
        padding-left: 0;
    }
}

.sman-pin-pic {
    background-image: url("./images/chip.png");
    width: 100px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.smancard-header-pic {
    background-image: url("./images/SMAN-Logo.png");
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    width: 100px;
}