/*body {
    height: inherit;
}*/

.url-main {
    width: 500px;
    background-color: white;
    padding: 6px;
}

input, textarea {
    width: 100%;
    position: relative;
    height: inherit;

    font-size: 18px;

    border: 1px solid;
    border-radius: 4px;
    padding: 12px;
    border-color: orangered;
    box-sizing: border-box;
}

input:focus {
    box-shadow: 0 0 1.5px 1px orangered;
}

.input-bg {
    height: 40px;
    border-radius: 3px;
    border-top-width: 1px;
    border-left-width: 1px;

    padding: 4px;
}

.input-bg-color {
    background-color: #ff450059;
    color: orangered;
    font-weight: 400;
}

.profile-section ul {
    list-style: none;
    display: flex;

    margin: 0;
    box-sizing: border-box;
}

.profile-section ul li {
    margin-left:4px;
    position: relative;
}

.profile-button {
    background-color: white;
    color: orangered;
    padding: 6px 12px;
    font-size: 14px;
    margin-bottom: 0;
    border-radius: 4px;
    background-image: none;
    border: 1px solid transparent;
    font-family: "raleway-heavy", sans-serif;
}

/*b {
    color: orangered;
}*/

.breadcrumbs {
    width: 91%;
}

.breadcrumbs h1 {
    border-bottom: 2px solid orangered;
}

.url-main {
    width: 91%;
    background-color: inherit;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(298px, 1fr));
    justify-content: space-around;
    grid-gap: 10px;
}

@keyframes dimmerAnim {
    0% { opacity: 0.4; }
    100% { opacity: 0.5; }
}

.dimmer {
    height: 10px;
    background-color: silver;
    animation-name: dimmerAnim;
    animation-duration: 900ms;
    animation-delay: 900ms;
    animation-iteration-count: infinite;
}


.link {
    background-color: white;
    padding: 8px;
    /* border: 1px solid orangered; */
    cursor: pointer;

    box-shadow: 0 2px 3px 0 #808080d6;
}

.link-info {
    display: flex;
    align-items: center;
}

.mlink-info {
    font-family: raleway-bold;
}

.link-info div {
    width: 100%;
    font-size: 13px;
}

.link-info-line {
    background-color: orangered;
    height: 1px;
}

.link-info-title {
    text-align: center;
}

.link-date-created {
    font-size: 0.75em;
    line-height: 1.4;
    letter-spacing: 0.059em;
}

.link div {
    padding: 1px;
}

.link-title {
    font-size: 20px;
    margin-bottom: 11px;

    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.link-url {
    font-size: 14px;
}

.link-url-short {
    font-size: 16px;
    color: orangered;
}

.link-visits {
    border-top: 1px solid orangered;
}

.message {
    text-align: left;
    border-left: 2px solid orangered;
    padding: 14px;
    background-color: rgba(233, 76, 0, 0.31);
    border-radius: 3px;
}

.info {
    text-align: left;
    border: 1px solid orangered;
    padding: 14px;
    background-color: rgba(233, 76, 0, 0.31);
    border-radius: 3px;

    margin: 3px;
    margin-bottom: 20px;
}

.modal-cnt {
    position: fixed;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1000;
}

.modal-backdrop {
    opacity: 0.5;
    width: inherit;
    height: inherit;
    background-color: grey;
    position: fixed;
}

.modal {
    background-color: white;
    z-index: 1;
    padding: 10px;
    margin-top: 10px;

    width: 520px;
    /* width: auto; */
    border: 1px solid orangered;
    box-shadow: 0px 11px 15px -7px rgba(0,0,0,0.2), 0px 24px 38px 3px rgba(0,0,0,0.14), 0px 9px 46px 8px rgba(0,0,0,0.12);
    border-radius: 4px;
}

.modal-head {
    height: 48px;
    background-color: rgba(233, 76, 0, 0.7);
    width: 100%;
}

.modal-head i {
    color: white;
    text-align: center;
    height: inherit;
    font-size: 40px;
    float: right;
    padding-right: 13px;

    padding-top: 2px;
}

.modal-head-mobile {
    display: none;
}

.modal-body {
    padding: 10px;
}

.close {
    display: none !important;
}

.modal-body select {
    /* */
}

.modal-input-bg  {
    padding: 4px;
    margin: 8px;
    padding: 2px;
    margin-left: 0;
    height: auto !important;
    word-wrap: anywhere;
}

@media(max-width: 500px) {
    .breadcrumbs {
        width: 91%;
    }            

    .modal {
        width: 100%;
        height: 100%;
        margin-top: 0;
        padding-top: 0;
    }

    .modal-head-mobile {
        display: block;
    }

    .link {
        margin-left: 15px;
        margin-right: 15px;
    }
}